.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  overflow: hidden;
}

h2 {
  font-weight: 400;
}

.container {
  display: flex;
}

@media(max-width: 600px) {
  .container {
    flex-direction: column;
  }
}

.form-container {
  margin-top: 10px;
  margin-bottom: 40px;
}

.form-container h2 {
  margin: 20px;
}

.form-container input {
  width: 30px;
  text-align: center;
}
